
import { Link, Route, Routes } from 'react-router-dom';
import { Container, Row, Nav, Navbar } from 'react-bootstrap';
import Collapse from 'react-bootstrap/Collapse';
import Fade from 'react-bootstrap/Fade';
import { useState } from 'react';
import data from './data/data.json'
import './App.css';
import DOMPurify from 'dompurify';

const buttonClasses = 'btn btn-light btn-sm font-monospace';


function About() {
  return(
    <Container className="mt-4">
      <main>
        <p className="lead" id="about">
          <span dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(data.about)}}/>
        </p>
      </main>
    </Container>
  );
}

function PdfIcon() {
  return(
    <span className="fa fa-file-pdf-o" title="Get PDF"></span>
  );
}

function VideoIcon() {
  return(
    <span className="fa fa-video-camera"></span>
  );
}


function GetPublication(publication, i) {
  let destId = 'Abstract-content' + i;
  let linkId = 'publication_' + i;
  let toolIcon = publication.tool_display;

  const [abstract_btn_displayed, set_abstract_btn_displayed] = useState(false);
  const [in_region, set_in_region] = useState(false);


  return(
    <li className="position-relative list-unstyled mt-3" id={linkId} key={linkId} onMouseOver={() => set_in_region(true)} onMouseLeave={() => set_in_region(false)}>
      <div className="col-lg-7 d-inline-block pubH">
        <h4 className="text-uppercasea pubTitle">{publication.title}</h4>
        <span id="authornames" dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(publication.authors)}}/>
        <br/>
        <span id="venue" dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(publication.venue)}}/>
        <br/>
        {publication["notes"] ? <><span id="notes" className="publication-notes" dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(publication.notes)}}/> <br/></> : null}
        {publication.pdf_link ? <a className={buttonClasses} href={publication.pdf_link} role="button"> <PdfIcon /> </a> : null}
        {publication.video_link ? <a className={buttonClasses} href={publication.video_link} role="button"> <VideoIcon /> </a> : null}
        {publication.arxiv_link ? <a className={buttonClasses} href={publication.arxiv_link}>Preprint</a> : null}
        {publication.bioxiv_link ? <a className={buttonClasses} href={publication.biorxiv_link}>Preprint</a> : null}
        {publication.tr_link ? <a className={buttonClasses} href={publication.tr_link}>TechRep</a> : null}
        {publication.proofs_link ? <a className={buttonClasses} href={publication.proofs_link}>Proofs</a> : null}
        <button className={buttonClasses.concat(' d-lg-none')} onClick={() => set_abstract_btn_displayed(!abstract_btn_displayed)}>Abstract</button>
        {publication.tool_link ? <a className={buttonClasses} href={publication.tool_link} dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(toolIcon)}}/> : null}
      </div>
      <div className='col-lg-5 float-end mb-3 d-lg-none abstract-contents'>
        <Collapse in={abstract_btn_displayed} >
          <div id={destId} dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(publication.abstract)}}/>
        </Collapse>
      </div>
      {
        in_region?
        <div className='col-lg-5 float-end mb-3 d-none d-lg-block d-xl-block abstract-contents'>
          <Fade in={in_region} timeout={1000} appear mountOnEnter>
            <div id={destId} dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(publication.abstract)}}/>
          </Fade>
        </div> :
      null
}
    </li>
  );
}


function Publications() {
  return(
    <Container className="mt-4">
      <main>
        <div id="publications-list" style={{display: "block"}}>
          <ul className="position-relative list-unstyled mt-3">
            {data.publications.map((publication, index) => (
              GetPublication(publication,index)
            )
            )}   
            <li className="list-unstyled"></li>
            <br/><br/><br/><br/><br/>
          </ul>
        </div>
      </main>
    </Container>
  );
}

export function App() {
  return(
  <>
  <Header />
  <Routes>
  <Route path='/' element={<About />} />
  <Route path='/about' element={<About />}>
  </Route>
  <Route path='/publications' element={<Publications />} />
  <Route path='*' element={<Container className='text-center'><h1>Page not found</h1></Container>} />
  </Routes>
  </>
);
}

function Header() {
  return(
    <header>
    <Container id="hola" fluid="xl" className="clearfix">
    <div>
      <Row>
        <article className="col-6 d-none d-sm-block pl-0">
        </article>
        <article className="col mt-5 ml-md-2 intro pt-2">
          <h1 className="text-uppercasea">Inigo Incer</h1>
          <h4 id="affiliation">
            Incoming Assistant Professor
            <br/>Electrical and Computer Engineering
            <br/>University of Michigan
          </h4>
          <p></p>
          <h6 className="font-monospace">
            iir [at] umich.edu
          </h6>
        </article>
      </Row>
      
      <p></p>
      
      <Navbar bg='transparent' variant='dark' expand='sm'>
      <Container fluid="xl">
        <Navbar.Toggle aria-controls='responsive-navbar-nav' /> 
        <Navbar.Collapse id='responsive-navbar-nav'>
          <Nav className='mr-sm-auto font-monospace'>
            <Nav.Link as={Link} to='/about'>About</Nav.Link>
            <Nav.Link as={Link} to='/publications'>Publications</Nav.Link>
          </Nav>
        </Navbar.Collapse>

      </Container>
      </Navbar>
      
      </div>
    </Container>
    </header>
  );
}

export default App;